a {
    text-decoration: none;
    color: rgb(47, 161, 228);
}

a:visited {
    color: rgb(47, 161, 228);
}

nav {
  display: flex;
  justify-content: space-around;
  align-items: center;
  min-height: 10vh;
  background: rgb(73, 79, 82);
  color: white;
}

.nav-links {
    width: 33%;
    display: flex;
    justify-content: space-around;
    align-items: center;
    list-style: none;
    text-decoration: none;
}